html,
body {
  font-family: "Noto Sans TC", sans-serif, Arial, "Microsoft JhengHei", Helvetica;
  font-size: 16px;
  line-height: 1.5;
  color: #000; }

a,
button {
  outline: none; }

[data-fade],
[data-fade-up],
[data-fade-left],
[data-fade-right],
[data-fade-zoom] {
  opacity: 0; }

@keyframes gradient {
  0% {
    background-position: 0 0; }
  100% {
    background-position: 200% 0; } }

/*
 * Remove text-shadow in selection highlight:
 * https://twitter.com/miketaylr/status/12228805301
 *
 * Vendor-prefixed and regular ::selection selectors cannot be combined:
 * https://stackoverflow.com/a/16982510/7133471
 *
 * Customize the background color to match your design.
 */
::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

/*
 * A better looking default horizontal rule
 */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0; }

/*
   * Remove the gap between audio, canvas, iframes,
   * images, videos and the bottom of their containers:
   * https://github.com/h5bp/html5-boilerplate/issues/440
   */
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle; }

/*
 * Remove default fieldset styles.
 */
fieldset {
  border: 0;
  margin: 0;
  padding: 0; }

/*
 * Allow only vertical resizing of textareas.
 */
textarea {
  resize: vertical; }

/*
 * Browser Upgrade Prompt
 */
.browserupgrade {
  position: absolute;
  top: 0;
  display: block;
  width: 100%;
  margin: 0.2em 0;
  background: #ccc;
  padding: 0.2em 0; }

html,
body {
  height: 100%; }

/* ------------------------------- Header ------------------------------- */
.header {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100px;
  padding: 20px 25px 20px 50px;
  box-shadow: 0 0 46px 0 rgba(0, 0, 0, 0.28);
  background-color: #fff;
  z-index: 99; }
  .header-logo a {
    display: block;
    width: 149px; }
    .header-logo a img {
      width: 100%; }
  .header-burger {
    display: none;
    width: 30px;
    height: 30px;
    background-image: url(../images/icon/btn/menu.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center; }
  .header.open .header-burger {
    background-image: url(../images/icon/btn/cross.svg); }
  @media only screen and (max-width: 1439px) {
    .header {
      height: 68px;
      padding: 12px 10px 12px 30px; }
      .header-logo a {
        width: 110px; } }
  @media only screen and (max-width: 1023px) {
    .header {
      height: 60px;
      padding: 12px 20px; }
      .header-logo a {
        width: 90px; }
      .header-burger {
        display: block; }
      .header-nav {
        position: fixed;
        top: 60px;
        left: 0;
        transform: translateX(-100%);
        box-sizing: border-box;
        width: 100%;
        height: calc(100% - 60px);
        border-top: 1px solid #d3d3d4;
        background-color: #fff;
        opacity: 0;
        z-index: 90;
        transition: all .4s; }
      .header.open .header-nav {
        transform: none;
        opacity: 1; } }

.nav {
  display: flex;
  align-items: center; }
  .nav-item {
    padding: 8px 45px;
    font-size: 18px;
    text-decoration: none;
    color: #000; }
    .nav-item + .nav-item {
      border-left: 1px solid #8b8b8b; }
    .nav-item:hover {
      color: #0091b1; }
    .nav-item.-active {
      color: #72cc80; }
  @media only screen and (max-width: 1439px) {
    .nav-item {
      padding: 3px 30px;
      font-size: 18px; } }
  @media only screen and (max-width: 1023px) {
    .nav {
      box-sizing: border-box;
      display: block;
      max-width: 500px;
      margin: auto;
      padding: 50px; }
      .nav-item {
        display: block;
        padding: 20px 30px;
        text-align: center; }
        .nav-item + .nav-item {
          border-left: none;
          border-top: 1px solid #8b8b8b; } }

/* ------------------------------- Main ------------------------------- */
.main {
  padding-top: 100px;
  overflow: hidden; }
  @media only screen and (max-width: 1439px) {
    .main {
      padding-top: 68px; } }
  @media only screen and (max-width: 1023px) {
    .main {
      padding-top: 60px; } }

/* ------------------------------- Footer ------------------------------- */
.footer {
  text-align: center; }
  .footer-name {
    padding: 10px;
    color: #848484; }
  .footer-content {
    background-color: #efeeee; }
  .footer-wrap {
    box-sizing: border-box;
    display: flex;
    align-items: flex-start;
    max-width: 1200px;
    margin: auto;
    padding: 40px 20px;
    text-align: left; }
  .footer-logo {
    flex: 0 0 auto;
    margin-right: 108px; }
    .footer-logo a {
      display: inline-block; }
    .footer-logo img {
      width: 150px; }
  .footer-links {
    display: flex;
    margin-right: 38px;
    padding: 30px 0; }
    .footer-links ul {
      min-width: 155px;
      margin: 0;
      padding: 0;
      list-style: none; }
      .footer-links ul + ul {
        margin-left: 50px; }
      .footer-links ul li:first-child {
        margin-bottom: 12px;
        font-size: 20px;
        font-weight: 500;
        color: #222; }
      .footer-links ul li + li {
        margin-top: 10px; }
      .footer-links ul li a {
        color: #8b8b8b;
        text-decoration: none; }
        .footer-links ul li a:hover {
          text-decoration: underline; }
  .footer-social {
    padding: 22px 0; }
    .footer-social a {
      display: inline-block;
      width: 42px;
      height: 42px; }
      .footer-social a:hover {
        opacity: 0.7; }
      .footer-social a + a {
        margin-left: 10px; }
      .footer-social a img {
        width: 100%; }
  .footer-copyRight {
    padding: 10px;
    font-size: 12px;
    letter-spacing: 1px;
    color: #fff;
    background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD); }
  @media only screen and (max-width: 1580px) {
    .footer-name {
      font-size: 14px; } }
  @media only screen and (max-width: 1023px) {
    .footer-wrap {
      justify-content: center;
      flex-wrap: wrap;
      padding: 20px; }
    .footer-logo {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
      text-align: center; }
      .footer-logo img {
        width: 90px; }
    .footer-links {
      padding: 5px 0; }
      .footer-links ul + ul {
        margin-left: 30px; }
      .footer-links ul li {
        font-size: 12px; }
        .footer-links ul li:first-child {
          margin-bottom: 8px;
          font-size: 16px; }
        .footer-links ul li + li {
          margin-top: 5px; }
    .footer-social {
      padding: 0; }
      .footer-social a {
        width: 30px;
        height: 30px; } }
  @media only screen and (max-width: 767px) {
    .footer-wrap {
      align-items: center;
      justify-content: space-between; }
    .footer-logo {
      order: 1;
      width: auto;
      margin-bottom: 0; }
    .footer-links {
      order: 3;
      flex: 0 0 100%;
      justify-content: space-between;
      margin-right: 0;
      margin-top: 20px;
      padding: 0; }
      .footer-links ul {
        min-width: auto; }
        .footer-links ul + ul {
          margin-left: 25px; }
    .footer-social {
      order: 2; } }

/* ------------------------------- Floating tool ------------------------------- */
.gotop {
  position: fixed; }

.main {
  position: relative;
  display: flex;
  align-items: center;
  min-height: calc(100% - 82px);
  padding-top: 0;
  background-image: url(./../images/bg/light/coming.png);
  background-repeat: no-repeat;
  background-position: top right;
  background-size: 20%;
  overflow: hidden; }
  @media only screen and (max-width: 1580px) {
    .main {
      min-height: calc(100% - 79px); } }
  @media only screen and (max-width: 900px) {
    .main {
      background: none; } }

.comingBlock {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 6.5vh 20px 4vh;
  letter-spacing: 1.5px;
  text-align: center; }
  .comingBlock-head {
    position: absolute;
    top: 30px;
    left: 56px;
    width: 186px; }
    .comingBlock-head img {
      width: 100%; }
  .comingBlock-contact {
    margin-top: 4vh; }
  @media only screen and (max-width: 1580px) {
    .comingBlock {
      padding: 5vh 20px 4vh; }
      .comingBlock-head {
        left: 40px;
        width: 150px; } }
  @media only screen and (max-width: 900px) {
    .comingBlock {
      padding: 8vh 20px 6vh; }
      .comingBlock-head {
        top: 15px;
        left: 15px;
        width: 100px; } }

@keyframes block {
  0% {
    transform: rotate(-8deg); }
  100% {
    transform: rotate(8deg); } }

.comingMsg-pic {
  transform: rotate(-8deg);
  transform-origin: top center;
  animation: block 2s ease-in-out infinite alternate; }
  .comingMsg-pic img {
    width: 173px; }

.comingMsg-title {
  margin-top: 2.5vh;
  font-size: 18px;
  font-weight: 500; }

.comingMsg-bar {
  position: relative;
  display: inline-block;
  width: 100%;
  max-width: 684px;
  height: 16px;
  margin: 5vh 0;
  border-radius: 8px;
  background-image: linear-gradient(to right, #1A8FAD, #56B770, #F8D074, #56B770, #1A8FAD);
  background-size: 200% 100%;
  animation: gradient 5s linear infinite reverse; }
  .comingMsg-bar::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 30%;
    height: 100%;
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0), white); }

.comingMsg-about {
  font-size: 21px;
  color: #4f969d; }

@media only screen and (max-width: 1580px) {
  .comingMsg-bar {
    margin: 4vh 0; }
  .comingMsg-pic img {
    width: 150px; }
  .comingMsg-bar {
    height: 12px; }
  .comingMsg-about {
    font-size: 18px; } }

@media only screen and (max-width: 1380px) {
  .comingMsg-pic img {
    width: 125px; }
  .comingMsg-title {
    font-size: 15px; }
  .comingMsg-bar {
    max-width: 540px; }
  .comingMsg-about {
    font-size: 16px; } }

@media only screen and (max-width: 900px) {
  .comingMsg-pic img {
    width: 118px; }
  .comingMsg-title {
    font-size: 14px; }
  .comingMsg-bar {
    height: 8px; }
  .comingMsg-about {
    font-size: 16px; } }

.contact {
  display: flex;
  justify-content: center; }
  .contact-item {
    box-sizing: border-box;
    flex: 0 1 250px;
    margin: 0 -1.5px;
    padding: 10px 10px 20px;
    text-decoration: none;
    border-left: 3px solid #e5e5e5;
    border-right: 3px solid #e5e5e5; }
    .contact-item:first-child {
