@import "./_variables";
@import "./_mixins";
@import "./_default";

@import "./_layout";

.main {
    position: relative;
    display: flex;
    align-items: center;
    min-height: calc(100% - 82px);
    padding-top: 0;
    background-image: url(./../images/bg/light/coming.png);
    background-repeat: no-repeat;
    background-position: top right;
    background-size: 20%;
    overflow: hidden;

    @media only screen and (max-width: 1580px) {
        min-height: calc(100% - 79px);
    }

    @media only screen and (max-width: 900px) {
        background: none;
    }
}

.comingBlock {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 6.5vh 20px 4vh;
    letter-spacing: 1.5px;
    text-align: center;

    &-head {
        position: absolute;
        top: 30px;
        left: 56px;
        width: 186px;

        img {
            width: 100%;
        }
    }

    &-contact {
        margin-top: 4vh;
    }

    @media only screen and (max-width: 1580px) {
        padding: 5vh 20px 4vh;

        &-head {
            left: 40px;
            width: 150px;
        }
    }

    @media only screen and (max-width: 900px) {
        padding: 8vh 20px 6vh;

        &-head {
            top: 15px;
            left: 15px;
            width: 100px;
        }
    }
}


@keyframes block {
    0% {
        transform: rotate(-8deg);
    }

    100% {
        transform: rotate(8deg);
    }
}

.comingMsg {

    &-pic {
        transform: rotate(-8deg);
        transform-origin: top center;
        animation: block 2s ease-in-out infinite alternate;

        img {
            width: 173px;
        }
    }

    &-title {
        margin-top: 2.5vh;
        font-size: 18px;
        font-weight: 500;
    }

    &-bar {
        position: relative;
        display: inline-block;
        width: 100%;
        max-width: 684px;
        height: 16px;
        margin: 5vh 0;
        border-radius: 8px;
        background-image: linear-gradient(to right, #1A8FAD, #56B770, #F8D074, #56B770, #1A8FAD);
        background-size: 200% 100%;
        animation: gradient 5s linear infinite reverse;

        &::after {
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            width: 30%;
            height: 100%;
            background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
        }
    }

    &-about {
        font-size: 21px;
        // font-weight: 300;
        color: #4f969d;
    }

    @media only screen and (max-width: 1580px) {
        &-bar {
            margin: 4vh 0;
        }

        &-pic {
            img {
                width: 150px;
            }
        }

        &-bar {
            height: 12px;
        }

        &-about {
            font-size: 18px;
        }
    }

    @media only screen and (max-width: 1380px) {
        &-pic {
            img {
                width: 125px;
            }
        }

        &-title {
            font-size: 15px;
        }

        &-bar {
            max-width: 540px;
        }

        &-about {
            font-size: 16px;
        }
    }

    @media only screen and (max-width: 900px) {
        &-pic {
            img {
                width: 118px;
            }
        }

        &-title {
            font-size: 14px;
        }

        &-bar {
            height: 8px;
        }

        &-about {
            font-size: 16px;
        }
    }
}

.contact {
    display: flex;
    justify-content: center;

    &-item {
        box-sizing: border-box;
        flex: 0 1 250px;
        margin: 0 -1.5px;
        padding: 10px 10px 20px;
        text-decoration: none;
        border-left: 3px solid #e5e5e5;
        border-right: 3px solid #e5e5e5;

        &:first-child {
            border-left: none;
        }

        &:last-child {
            border-right: none;
        }
    }

    &-pic {
        box-sizing: border-box;
        width: 115px;
        height: 115px;
        margin: auto;
        padding: 14px;

        img {
            width: 100%;
        }
    }

    &-text {
        display: block;
        margin-top: 10px;
        font-size: 18px;
        text-decoration: none;
        // font-weight: 300;
        color: #4f969d;
    }

    &-item.qrCode & {
        &-pic {
            padding: 0;
        }
    }

    @media only screen and (max-width: 1580px) {
        &-item {
            flex: 0 1 220px;
            margin: 0 -1px;
            padding: 5px 10px 10px;
            border-left: 2px solid #e5e5e5;
            border-right: 2px solid #e5e5e5;
        }

        &-pic {
            width: 90px;
            height: 90px;
        }

        &-text {
            margin-top: 5px;
            font-size: 16px;
        }
    }

    @media only screen and (max-width: 1380px) {
        &-item {
            flex: 0 1 200px;
        }

        &-pic {
            width: 70px;
            height: 70px;
            padding: 8px;
        }

        &-text {
            font-size: 14px;
        }
    }

    @media only screen and (max-width: 900px) {
        flex-wrap: wrap;
        justify-content: flex-start;
        margin: -12px 0;

        &-item {
            flex: 0 1 50%;
            margin: 12px 0;
            padding: 5px;
            border: none;

            &:nth-child(odd) {
                border-right: 1px solid #e5e5e5;
            }
        }

        &-pic {
            width: 80px;
            height: 80px;
        }
    }
}


@import "./_utilities";