html,
body {
    height: 100%;
}


/* ------------------------------- Header ------------------------------- */
.header {
    position: fixed;
    top: 0;
    left: 0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 100px;
    padding: 20px 25px 20px 50px;
    box-shadow: 0 0 46px 0 rgba(0, 0, 0, 0.28);
    background-color: #fff;
    z-index: 99;

    &-logo {
        a {
            display: block;
            width: 149px;

            img {
                width: 100%;
            }
        }
    }

    &-burger {
        display: none;
        width: 30px;
        height: 30px;
        background-image: url(../images/icon/btn/menu.svg);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &.open & {
        &-burger {
            background-image: url(../images/icon/btn/cross.svg);
        }
    }

    @include breakpoint-down('desktop') {
        height: 68px;
        padding: 12px 10px 12px 30px;

        &-logo {
            a {
                width: 110px;
            }
        }
    }

    @include breakpoint-down('tablet') {
        height: 60px;
        padding: 12px 20px;

        &-logo {
            a {
                width: 90px;
            }
        }

        &-burger {
            display: block;
        }

        &-nav {
            position: fixed;
            top: 60px;
            left: 0;
            transform: translateX(-100%);
            box-sizing: border-box;
            width: 100%;
            height: calc(100% - 60px);
            border-top: 1px solid #d3d3d4;
            background-color: #fff;
            opacity: 0;
            z-index: 90;
            transition: all .4s;
        }

        &.open & {
            &-nav {
                transform: none;
                opacity: 1;
            }
        }
    }
}

.nav {
    display: flex;
    align-items: center;

    &-item {
        padding: 8px 45px;
        font-size: 18px;
        text-decoration: none;
        color: #000;

        &+& {
            border-left: 1px solid #8b8b8b;
        }

        &:hover {
            color: #0091b1;
        }

        &.-active {
            color: #72cc80;
        }
    }

    @include breakpoint-down('desktop') {
        &-item {
            padding: 3px 30px;
            font-size: 18px;
        }
    }

    @include breakpoint-down('tablet') {
        box-sizing: border-box;
        display: block;
        max-width: 500px;
        margin: auto;
        padding: 50px;

        &-item {
            display: block;
            padding: 20px 30px;
            text-align: center;

            &+& {
                border-left: none;
                border-top: 1px solid #8b8b8b;
            }
        }
    }
}

/* ------------------------------- Main ------------------------------- */
.main {
    padding-top: 100px;
    overflow: hidden;

    @include breakpoint-down('desktop') {
        padding-top: 68px;
    }

    @include breakpoint-down('tablet') {
        padding-top: 60px;
    }
}

/* ------------------------------- Footer ------------------------------- */
.footer {
    text-align: center;

    &-name {
        padding: 10px;
        color: #848484;
    }

    &-content {
        background-color: #efeeee;
    }

    &-wrap {
        box-sizing: border-box;
        display: flex;
        align-items: flex-start;
        max-width: 1200px;
        margin: auto;
        padding: 40px 20px;
        text-align: left;
    }

    &-logo {
        flex: 0 0 auto;
        margin-right: 108px;

        a {
            display: inline-block;
        }

        img {
            width: 150px;
        }
    }

    &-links {
        display: flex;
        margin-right: 38px;
        padding: 30px 0;

        ul {
            min-width: 155px;
            margin: 0;
            padding: 0;
            list-style: none;

            &+ul {
                margin-left: 50px;
            }

            li {
                &:first-child {
                    margin-bottom: 12px;
                    font-size: 20px;
                    font-weight: 500;
                    color: #222;
                }

                &+li {
                    margin-top: 10px;
                }

                a {
                    color: #8b8b8b;
                    text-decoration: none;

                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    &-social {
        padding: 22px 0;

        a {
            display: inline-block;
            width: 42px;
            height: 42px;

            &:hover {
                opacity: 0.7;
            }

            &+a {
                margin-left: 10px;
            }

            img {
                width: 100%;
            }
        }
    }

    &-copyRight {
        padding: 10px;
        font-size: 12px;
        letter-spacing: 1px;
        color: #fff;
        background-image: linear-gradient(to right, #F8D074, #56B770, #1A8FAD);
    }

    @media only screen and (max-width: 1580px) {
        &-name {
            font-size: 14px;
        }
    }

    @include breakpoint-down('tablet') {
        &-wrap {
            justify-content: center;
            flex-wrap: wrap;
            padding: 20px;
        }

        &-logo {
            width: 100%;
            margin-right: 0;
            margin-bottom: 20px;
            text-align: center;

            img {
                width: 90px;
            }
        }

        &-links {
            padding: 5px 0;

            ul {
                &+ul {
                    margin-left: 30px;
                }

                li {
                    font-size: 12px;

                    &:first-child {
                        margin-bottom: 8px;
                        font-size: 16px;
                    }

                    &+li {
                        margin-top: 5px;
                    }
                }
            }
        }

        &-social {
            padding: 0;

            a {
                width: 30px;
                height: 30px;
            }
        }
    }

    @include breakpoint-down('mobile') {
        &-wrap {
            align-items: center;
            justify-content: space-between;
        }

        &-logo {
            order: 1;
            width: auto;
            margin-bottom: 0;
        }

        &-links {
            order: 3;
            flex: 0 0 100%;
            justify-content: space-between;
            margin-right: 0;
            margin-top: 20px;
            padding: 0;

            ul {
                min-width: auto;

                &+ul {
                    margin-left: 25px;
                }
            }
        }

        &-social {
            order: 2;
        }
    }
}

/* ------------------------------- Floating tool ------------------------------- */

.gotop {
    position: fixed;
}